export default {
	data() {
		return {
			userInfo: {}
		}
	},
	methods: {
		// 操作头像
		operator() {
			let userInfo = this.getGlobalUser('userInfo');
			uni.showActionSheet({
				itemList: ["查看我的头像", "从相册中选择上传"],
				success: res => {
					if (res.tapIndex == 0) {
						// 预览头像
						let faceArray = []
						faceArray.push(this.userInfo.faceImage)
						// 头像预览
						uni.previewImage({
							urls: faceArray,
							current: faceArray[0]
						})
					} else if (res.tapIndex == 1) {
						// 选择上传头像
						uni.chooseImage({
							count: 1,
							sizeType: ["compressed"],
							sourceType: ["album"],
							success: (res) => {
								// 获得临时路径
								let tempFilePaths = res.tempFilePaths[0];







								uni.navigateTo({
									url: "../faceCrop/faceCrop?tempFilePaths=" + tempFilePaths
								})

							}
						})
					}
				}
			});
		},
		// 用户退出登录
		logout() {
			// 获取localStorage里面的用户信息
			let userInfo = this.getGlobalUser('userInfo');
			// 发送退出登录的请求
			uni.request({
				url: this.baseUrl + `/user/logout?userId=${userInfo.id}&&qq=1335436466`,
				method: 'POST',
				success: (res) => {
					if (res.data.status == 200) {
						uni.removeStorageSync("userInfo");
						uni.switchTab({
							url: '../me/me'
						})
					}
				}
			})

		},
		// 清理缓存按钮
		clearStorage() {
			uni.clearStorageSync();
			uni.showToast({
				title: "清理缓存成功",
				mask: false,
				duration: 1500
			})
		}
	},
	onShow() {
		let userInfo = this.getGlobalUser('userInfo');
		this.userInfo = userInfo;
	}
}
